.toast-title {
  font-weight: bold;
}

.Toastify__toast-body {
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  a,
  label {
    color: #ffffff;
  }
  a:hover {
    color: #cccccc;
    text-decoration: none;
  }
}

.Toastify__close-button {
  position: relative;
  float: right;
  font-size: 0.9rem;
  font-weight: bold;
  color: #ffffff;
  opacity: 0.9;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=90);
  filter: alpha(opacity=90);
  line-height: 1;
  &:hover,
  &:focus {
    color: #000000;
    text-decoration: none;
    cursor: pointer;
    opacity: 0.4;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
    filter: alpha(opacity=40);
  }
}

.rtl .Toastify__close-button {
  left: -0.3em;
  float: left;
  right: 0.3em;
}

/*Additional properties for button version
iOS requires the button element instead of an anchor tag.
If you want the anchor version, it requires `href="#"`.*/
button.Toastify__close-button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
}

.Toastify__toast-container--top-center {
  top: 0;
  right: 0;
  width: 100%;
}

.Toastify__toast-container--bottom-center {
  bottom: 0;
  right: 0;
  width: 100%;
}

.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%;
}

.toast-bottom-full-width {
  bottom: 0;
  right: 0;
  width: 100%;
}

.Toastify__toast-container--top-left {
  top: 12px;
  left: 12px;
}

.Toastify__toast-container--top-right {
  top: 12px;
  right: 12px;
}

.Toastify__toast-container--top-right {
  right: 12px;
  bottom: 12px;
}

.Toastify__toast-container--bottom-left {
  bottom: 12px;
  left: 12px;
}

.Toastify__toast-container {
  position: fixed;
  z-index: 999999;
  pointer-events: none;
  padding: 0;
  width: auto;
  * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  > div {
    position: relative;
    pointer-events: auto;
    overflow: hidden;
    margin: 0 0 6px;
    min-height: inherit;
    width: 300px;
    padding: 15px 15px 15px 50px;
    -moz-border-radius: 3px 3px 3px 3px;
    -webkit-border-radius: 3px 3px 3px 3px;
    border-radius: 3px 3px 3px 3px;
    background-position: 15px center;
    background-repeat: no-repeat;
    -moz-box-shadow: 0 0 12px #999999;
    -webkit-box-shadow: 0 0 12px #999999;
    box-shadow: 0 0 12px #999999;
    color: #ffffff;
    opacity: 0.9;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=90);
    filter: alpha(opacity=90);
    &.rtl {
      direction: rtl;
      padding: 15px 50px 15px 15px;
      background-position: right 15px center;
    }
    &:hover {
      -moz-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.125);
      -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.125);
      box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.125);
      opacity: 1;
      -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
      filter: alpha(opacity=100);
      cursor: pointer;
    }
  }
  > .Toastify__toast--info {
    background-image: url("data:image/png;base64,") !important;
  }
  > .Toastify__toast--error {
    background-image: url("data:image/png;base64,") !important;
  }
  > .Toastify__toast--success {
    background-image: url("data:image/png;base64,") !important;
  }
  > .Toastify__toast--warning {
    background-image: url("data:image/png;base64,") !important;
  }
  &.toast-top-center > div,
  &.toast-bottom-center > div {
    width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
  &.toast-top-full-width > div,
  &.toast-bottom-full-width > div {
    width: 96%;
    margin-left: auto;
    margin-right: auto;
  }
}

.Toastify__toast {
  background-color: #030303;
}

.Toastify__toast--success {
  background-color: #0abb87;
}

.Toastify__toast--error {
  background-color: #fd397a;
}

.Toastify__toast--info {
  background-color: #5578eb;
}

.Toastify__toast--warning {
  background-color: #ffb822;
}

.Toastify__progress-bar {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  background-color: #000000;
  opacity: 0.4 !important;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
  filter: alpha(opacity=40);
}

/*Responsive Design*/
@media all and (max-width: 240px) {
  .Toastify__toast-container {
    > div {
      padding: 8px 8px 8px 50px;
      width: 11em;
      &.rtl {
        padding: 8px 50px 8px 8px;
      }
    }
    .Toastify__close-button {
      right: -0.2em;
      top: -0.2em;
    }
    .rtl .Toastify__close-button {
      left: -0.2em;
      right: 0.2em;
    }
  }
}

@media all and (min-width: 241px) and (max-width: 480px) {
  .Toastify__toast-container {
    > div {
      padding: 8px 8px 8px 50px;
      width: 18em;
      &.rtl {
        padding: 8px 50px 8px 8px;
      }
    }

    .Toastify__close-button {
      right: -0.2em;
      top: -0.2em;
    }

    .rtl .Toastify__close-button {
      left: -0.2em;
      right: 0.2em;
    }
  }
}

@media all and (min-width: 481px) and (max-width: 768px) {
  .Toastify__toast-container {
    > div {
      padding: 15px 15px 15px 50px;
      width: 25em;
      &.rtl {
        padding: 15px 50px 15px 15px;
      }
    }
  }
}
