.letterHead {
	text-align: center;
	position: relative;
	margin-bottom: 2em;
	.heading {
		margin: 1rem 0 1rem !important;
		color: #e6130b !important;
		div {
			margin: 0.5em 0 !important;
		}
		span {
			padding: 0;
			line-height: 1.5em;
			display: block;
		}
		.nepal-sarkar-logo {
			position: absolute;
			top: 0;
			left: 0;
			padding: 0;
			width: 6rem;
		}
		.illam-logo {
			position: absolute;
			top: 0;
			right: 0;
			padding: 0;
			width: 9rem;
		}

		.large {
			font-size: 2em;
		}
		.medium {
			font-size: 1.5em;
		}
		.small {
			font-size: 1em;
		}

		.no-color {
			color: black !important;
		}
		.bold {
			font-weight: bold;
		}
	}
	address {
		font-style: normal;
		margin: 0 0 1rem;
		h3 {
			margin: 0 0 0.5rem !important;
		}
	}
}

.letterHead-compact {
	text-align: center;
	.heading {
		margin: 0 0 0.7rem !important;
		color: #e6130b !important;
		div {
			margin: 0.25em 0 !important;
		}
		span {
			padding: 0;
			line-height: 1.5em;
			display: block;
		}
		.nepal-sarkar-logo {
			position: absolute;
			top: 0;
			left: 0;
			padding: 0;
			width: 1rem;
		}
		.illam-logo {
			position: absolute;
			top: 0;
			right: 0;
			padding: 0;
			width: 9rem;
		}

		.large {
			font-size: large;
		}
		.medium {
			font-size: medium;
		}
		.small {
			font-size: x-small;
		}

		.no-color {
			color: black !important;
		}
		.bold {
			font-weight: bold;
		}
	}
	address {
		font-style: normal;
		margin: 0 0 1rem;
		h3 {
			margin: 0 0 0.5rem !important;
		}
	}
}

