@import 'fonts';

$app-fontSize: 16px;
$app-fontWeight: normal;
$app-primaryColor: #644796;
$app-primaryLightColor: #BEB8EB;
$app-primaryDarkColor: #5e3e94;
$app-secondaryColor: #18bbcb;
$app-secondaryDarkColor: #2d2a2f;
$app-textColor: #000000;
$app-titleColor: #2a3554;
$app-placeholderColor: #a2afcd;
$app-strokeColor: #d7dfef;
$btn-primaryColor: #ffffff;
$btn-secondaryColor: #1f0322;
$btn-error: #da4167;
$warn-color: #E47792;
$btn-success: #45cb85;
$border-strokeColor: #dfe6f4;
$app-lineHeight: 1.5em;
$app-dangerColor: #ed3445;
$app-infoColor: #6080f9;
$btn-primaryBgColor: #f2711c;
$btn-hoverColor: #e0630f;
$fontFamily: $defaultFont, $pronova-regular, Arial, Helvetica, sans-serif !important;
$fontFamilyEnglish: $pronova-regular, Arial, Helvetica, sans-serif !important;

@mixin transition {
	transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

@mixin site-btn {
	padding: 0.61em 1.875em !important;
	font-size: 0.94em !important;
	text-transform: uppercase !important;
	font-family: $fontFamily;
	background: $app-primaryColor !important;
	color: $btn-primaryColor !important;
	border: none !important;
	border-radius: 3.13em !important;
	min-width: 8em !important;
	min-height: inherit !important;
	line-height: inherit !important;
}

body {
	font-family: $fontFamily;
	font-size: $app-fontSize !important;
	font-weight: $app-fontWeight !important;
	line-height: $app-lineHeight !important;
	color: $app-textColor !important;
	background: none;
}

a {
	color: $app-secondaryColor;
	text-decoration: none;
	@include transition();
	&:hover,
	&:focus {
		text-decoration: none;
		color: $app-titleColor;
		outline: none;
	}
}

