.main-sidebar-navigation {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	padding: 47px 0;
	background: $app-secondaryDarkColor;
	// background: #1B1C1D;
	width: 250px;
	display: flex;
	flex-direction: column;
	flex: 1;
	overflow-x: hidden;
	.sidebarSearchBar {
		padding: 10px 10px 15px;
		.ui.input {
			width: 100%;
			input {
				background: rgba(255, 183, 183, 0.11);
				height: 40px;
				font-size: 14px;
				color: rgba(255, 255, 255, 0.8);
				border: none;
				&::-webkit-input-placeholder {
					color: rgba(255, 255, 255, 0.87);
				}
			}
		}
	}
	.header-removemargin {
		margin: 0 !important;
		color: #888 !important;
		padding: 8px 20px !important;
		background: #333342;

		.icon {
			font-size: 1rem !important;
			margin-bottom: 0.68rem !important;
		}
	}

	.sidebar-navigation {
		flex: 1;
		overflow-y: scroll;
		.sidebar-primary-menus {
			li {
				i {
					color: white;
				}
				list-style-type: none;
				display: block;
				font-size: 13px;
				color: rgba(255, 255, 255, 0.87);
				&:hover,
				&.active {
					color: rgb(0, 0, 0);
					background-color: #4a415b;
					// margin-top: 1px;
				}
				a {
					display: inline-block !important;
					width: 100%;
					padding: 3px 20px;
					display: block;
					color: rgba(255, 255, 255, 0.87);
					&.active {
						background: #574c6b;
					}
				}
				&.has-children {
					position: relative;
					.span-link {
						// margin: 10px;
						display: inline-block !important;
						width: 100%;
						padding: 6px 20px 2px 20px;
						background-color: transparent;
						&:hover {
							color: white;
							cursor: pointer;
						}
					}
					.has-item-children {
						color: #fff;
						// &:hover,
						// &.active {
						// 	// color: rgb(0, 0, 0);
						// 	// background-color: rgba(255, 255, 255, 0.87);
						// }
					}
					li {
						a {
							color: #cebceb;
						}
						padding: 0px 10px;
						&:hover a {
							color: white;
						}
					}
					ul {
						// background: rgba(255, 255, 255, 0.12);
						border-radius: 4px;
					}
					.sub-toggle {
						position: absolute;
						top: 0px;
						right: 0;
						font-size: 19px;
						line-height: initial;
						background: rgba(0, 0, 0, 0.3);
						padding: 0 5px;
						cursor: pointer;
						border-radius: 4px;
					}
				}
			}
		}
	}
	+ .site-main {
		margin-left: 250px;
	}
	+ .site-main-setup {
		margin-top: 10px;
		margin-left: 250px;
	}
}

